import { Button, Progress } from '@mantine/core';
import CSS from 'csstype';
import { Image } from '@mantine/core';
import { IconBrandTwitch } from '@tabler/icons-react';
class cp {
    Karakter: string;
    Kesz: number;
    Osszes: number;
    Szinkronhang: string;
    KepLink: string;
    Twitch: string;
    TwitchNev:string;
    Nem:string;
    constructor(karakter: string, kesz: number, osszes: number, szinkronhang: string, keplink: string, twitch: string,twitchnev:string,nem:string) {
        this.Karakter = karakter;
        this.Kesz = kesz;
        this.Osszes = osszes;
        this.Szinkronhang = szinkronhang;
        this.KepLink = keplink;
        this.Twitch = twitch;
        this.TwitchNev = twitchnev;
        this.Nem=nem;

    }
}
const cps: CSS.Properties =
{
    marginTop: '10px',
    display: 'inline-block',
    backgroundColor: 'rgba(150,150,150,50)',
    color: '#fff',

    position: 'relative',
    width: '75%',
    height: '220px',
    borderRadius: '7px'
}
const prog: CSS.Properties =
{
    display: 'inline-block',
    width: '75%',
    alignContent: 'center',
}
const img: CSS.Properties =
{
    position: 'absolute',
    right: '10px',
    top: '10px'
}
const h2: CSS.Properties = {
    marginTop: '0px',
    marginBottom: '-10px'
}
var percent = 0;
function CharacterProgress(props: cp) {
    percent = props.Kesz / props.Osszes * 100;
    var tw;
    if(props.Twitch !== '')
    {
        tw = <Button 
        color="violet" 
        leftIcon=<IconBrandTwitch />
        component="a"
        target="_blank"
        rel="noopener noreferrer"
        href={props.Twitch}

        >
            {props.TwitchNev}
        </Button>
    }
    else
    {
        tw = null
    }
    return (
        <>
            <div style={cps}>
                <h2 style={h2}>{props.Karakter} - {props.Nem}</h2><br />
                <h2 style={h2}>{props.Kesz}/{props.Osszes}</h2><br />
                <Progress color="green" radius="xl" size="20px" value={percent} striped label={percent + '%'} style={prog} />
                <Image height={150} width={150} src={props.KepLink} style={img} />
                <br />
                <h3>Magyar hangja: {props.Szinkronhang}</h3>
                {tw}
            </div>
        </>
    );
}
export default CharacterProgress;