import { Progress } from '@mantine/core';
import CSS from 'csstype';

const card: CSS.Properties =
{
    display: 'inline-block',
    backgroundColor: 'rgba(150,150,150,50)',
    color: '#fff',
    textAlign: 'center',
    alignContent: 'center',
    position: 'relative',
    width: '75%',
    height: '75px',
    borderRadius: '7px'
}
const prog: CSS.Properties = 
{
    display: 'inline-block',
    width: '75%',
    alignContent: 'center',
}
class allas {
    Kesz: number;

    constructor(kesz: number) {
        this.Kesz = kesz;
    }
}
var percent:number;
function OvProgress(props: allas) {
    percent = props.Kesz/3340*100
    return (
        <>
            <div style={card}>
                Jelenlegi llapot: {props.Kesz}/3340<br />
                <Progress color="green" radius="xl" size="30px" value={percent} striped label= {percent + '%'} style={prog}/>
            </div>
        </>
    );
}
export default OvProgress;